import { configureStore,createSlice } from "@reduxjs/toolkit";





let user = createSlice({               //createSlice()사용해서 테이터를 만든다
    name:'user',
    initialState: {name:'kim',age:20},                     //redux의 state변경하는 법-> state수정해주는 함수를 만들고, 원할떄 그 함수를 실행해 달라고 store.js에 요청해야한다. 
    reducers : { //'kim을 john kim으로 수정해주고싶어요'
        changeName(state){
            return state.age = state.age +1;
        },
        increase(state,a){
            state.age +=a.payload; //a 파라미터를 받기위해서는 .payload를 붙여줘야 파라미터가 들어올수있다. payload = 화물, 소포.
        }
    }
})
export let {changeName,increase} = user.actions;


let stock = createSlice({               //createSlice()사용해서 테이터를 만든다
    name:'stock',
    initialState: [10,11,12]
})


let cartProduct = createSlice({               //createSlice()사용해서 테이터를 만든다
    name:'cartProduct',
    initialState: [],
    reducers:{
        qtyIncrease(state, id){
           state[id.payload].count += 1;
        },
        qtyMinus(state, id){
            if( state[id.payload].count === 0){
                state[id.payload].count = 0;
            }else{
                state[id.payload].count =  state[id.payload].count - 1;
            }
        },
        qtyCal(state,id){
            state[id.payload].total= state[id.payload].price * state[id.payload].count;

        },
        addItem(state, item){
                state.push(item.payload);
                alert("Added " + '"'+item.payload.title +'"'+ " On your Cart")

        },
        removeItem(state, id){
           let copy = [...state]
           let copy2 = copy.filter((e)=>{return e.id !== id.payload;})//Filter 해당요소를 제외한 배열을 반환한다.
           return copy2
        },
        setQty(state, qty,id){

        }
    }
})
export let{qtyIncrease, qtyMinus, addItem,qtyCal, removeItem,setQty} = cartProduct.actions


export default configureStore({ // 생성한 데이터를 등록해야한다
    reducer: {
        user : user.reducer,   //작명: user.reducer .reducer를 붙여줘야한다
        stock : stock.reducer,
        cartProduct : cartProduct.reducer 
    }
})